@import "global.scss";
@import "./components/header";

@include reset();

* {
    box-sizing: border-box;
}

body {
    font-family: GothamPro;
    font-weight: 300;
    color: $--color-black-100;
}

.wrapper {
    @include container();
    @include xl(max-width, 1340px);
    @include xxl(max-width, 1600px);
}

.row {
    @include row-flex();
}

.col {
    @include col();
    @include size(12);
}

.site-container {
    padding-top: 94px;

    @include before('sm') {
        padding-top: 50px;
    }
}

.svg__icon {
    width: 22px;
    height: 22px;
    position: relative;

    @include before('sm') {
        width: 18px;
        height: 18px;
    }
}

.title__block {
    font-size: $--font-size-32;
    line-height: $--line-height-38;
    font-weight: $--font-weight-black;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    min-height: 110px;
    text-align: center;
    margin-bottom: 50px;

    &::before {
        content: '';
        background: url(../img/title__bg.png) no-repeat scroll 0 0;
        width: 210px;
        height: 110px;
        background-size: 100%;
        display: block;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        z-index: -1;
        opacity: 0.5;
    }

    @include before('sm') {
        font-size: $--font-size-22;
        line-height: $--line-height-30;
        margin-bottom: 25px;
    }
}

.main__button {
    border-radius: $--border-radius;
    border: 2px solid $--color-red-100;
    background-color: $--color-red-100;
    padding: 13px 40px 12px;
    color: $--color-white-200;
    font-size: $--font-size-16;
    line-height: $--line-height-20;
    font-weight: $--font-weight-medium;
    transition: $--transition-main;
    text-decoration: none;
    display: table;
    margin: 0 auto;
    cursor: pointer;

    &:hover,
    &:focus {
        background-color: $--color-white-100;
        color: $--color-red-100;
        transition: $--transition-main;
    }
}


.hover__image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparentize($--color-black-100, 0.7);
    opacity: 1;
    transition: $--transition-main;
    border-radius: $--border-radius;

    &-main {
        position: relative;
        width: 100%;
        height: 100%;
    }
}

.hover__image-product {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: transparentize($--color-black-100, 0.7);
    opacity: 0;
    transition: $--transition-main;
    border-radius: $--border-radius;

    &:hover,
    &:focus {
        opacity: 1;
    }
}


.all__category {
    margin-top: 40px;
    background-color: transparentize($--color-white-200, 0.4);
    padding: 30px 0;

    @include before('lg') {
        margin-top: 10px;
        padding: 20px 0;
    }
}

.sliader {
    background: url(../img/slaider.jpg) no-repeat scroll 50% 50% / cover;
    min-height: 400px;
}

.required {
    color: $--color-red-100;
}


.main__form {
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;

    @include after('sm') {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 20px;
    }

    @include after('md') {
        max-width: 70%;
        margin: 50px auto 0;
    }

    &-field {
        display: flex;
        flex-direction: column;
        margin-bottom: 30px;
        width: 100%;

        &--text {
            grid-column-start: 1;
            grid-column-end: 3;
        }
    }

    &-button {
        grid-column-start: 1;
        grid-column-end: 3;
        width: 100%;
    }

    &-prompt {
        flex-basis: 100%;
        margin-top: 30px;
    }

    &-licenze {
        font-size: $--font-size-12;
        line-height: $--line-height-16;
        text-align: center;
        font-weight: $--font-weight-medium;
    }

    &-label {
        font-size: $--font-size-14;
        line-height: $--line-height-20;
        font-weight: $--font-weight-medium;
        margin-bottom: 10px;
    }

    &-input {
        padding: 5px 15px;
        height: 45px;
        border: 1px solid transparentize($--color-black-100, 0.8);
        border-radius: $--border-radius;
        font-size: $--font-size-14;
        box-shadow: 0px 0px 10px rgba(13, 14, 15, 0.1);
        color: $--color-black-100;

        &::-webkit-input-placeholder {
            color: $--color-black-100;
        }

        &::-moz-placeholder {
            color: $--color-black-100;
        }

        &:-moz-placeholder {
            color: $--color-black-100;
        }

        &:-ms-input-placeholder {
            color: $--color-black-100;
        }

        &:focus {
            border: 1px solid transparentize($--color-black-100, 0.6);
            outline: none;
        }
    }

    &-textarea {
        padding: 10px 15px;
        height: 40px;
        border: 1px solid transparentize($--color-black-100, 0.8);
        box-shadow: 0px 0px 10px rgba(13, 14, 15, 0.1);
        border-radius: $--border-radius;
        font-size: $--font-size-14;
        min-height: 200px;
        line-height: $--line-height-20;
        color: $--color-black-100;

        &::-webkit-input-placeholder {
            color: $--color-black-100;
        }

        &::-moz-placeholder {
            color: $--color-black-100;
        }

        &:-moz-placeholder {
            color: $--color-black-100;
        }

        &:-ms-input-placeholder {
            color: $--color-black-100;
        }

        &:focus {
            border: 1px solid transparentize($--color-black-100, 0.6);
            outline: none;
        }
    }

    &-login {
        display: flex;
        max-width: 400px;
        margin-left: 0;

        .main__form-button {
            width: auto;
        }
    }

    &--contacts {
        max-width: 100%;
    }
}




h1 {
    margin: 20px 0;
    font-size: $--font-size-32;
    line-height: $--line-height-40;
    font-weight: $--font-weight-medium;

    @include before('xl') {
        font-size: $--font-size-26;
        line-height: $--line-height-32;
    }

    @include before('sm') {
        font-size: $--font-size-22;
        line-height: $--line-height-28;
    }
}

h2 {
    margin: 20px 0;
    font-size: $--font-size-30;
    line-height: $--line-height-34;
    font-weight: $--font-weight-medium;

    @include before('xl') {
        font-size: $--font-size-22;
        line-height: $--line-height-26;
    }

    @include before('sm') {
        font-size: $--font-size-20;
        line-height: $--line-height-22;
    }
}

h3 {
    margin: 20px 0;
    font-size: $--font-size-28;
    line-height: $--line-height-34;
    font-weight: $--font-weight-medium;

    @include before('xl') {
        font-size: $--font-size-20;
        line-height: $--line-height-24;
    }

    @include before('sm') {
        font-size: $--font-size-18;
        line-height: $--line-height-22;
    }
}


h4,
h5,
h6 {
    margin: 20px 0;
    font-size: $--font-size-22;
    line-height: $--line-height-28;
    font-weight: $--font-weight-medium;

    @include before('xl') {
        font-size: $--font-size-18;
        line-height: $--line-height-22;
    }
}


p {
    font-size: $--font-size-18;
    line-height: $--line-height-24;
    color: $--color-black-100;
    margin-bottom: 15px;

    @include before('sm') {
        font-size: $--font-size-16;
        line-height: $--line-height-22;
    }
}

ul {
    margin-left: 20px;
    margin-bottom: 15px;

    li {
        padding-left: 20px;
        position: relative;
        margin-bottom: 5px;

        &::before {
            content: '';
            width: 5px;
            height: 5px;
            border-radius: 50%;
            background-color: $--color-red-100;
            display: inline-block;
            position: absolute;
            left: 0;
            top: 7px;
        }
    }
}


ol {
    margin-left: 20px;
    margin-bottom: 20px;
    padding-left: 20px;

    li {
        padding-left: 0;
        position: relative;
        margin-bottom: 10px;
    }
}

header ul,
footer ul {
    margin: 0;
}

header ul li,
footer ul li {
    margin: 0;
    padding: 0;
}

header ul li::before,
footer ul li::before {
    display: none;
}


strong {
    font-weight: $--font-weight-black;
}


a {
    color: $--color-red-100;
    transition: $--transition-main;

    &:hover {
        text-decoration: none;
        color: transparentize($--color-red-100, 0.1);
        transition: $--transition-main;
    }
}


.main__contacts {
    margin: 0;

    &-item {
        display: flex;
        align-items: center;
        padding: 0;
        margin-bottom: 15px;

        @include before('sm') {
            flex-direction: column;
            align-items: flex-start;
            margin-right: 0;
            margin-bottom: 10px;
        }

        &::before {
            display: none;
        }
    }

    &-link {
        font-size: $--font-size-16;
        font-weight: $--font-weight-medium;
        display: flex;
        align-items: center;
        text-decoration: none;
        margin-right: 5px;

        @include before('sm') {
            margin-right: 0;
            margin-bottom: 10px;
        }

        &:hover,
        &:focus {
            color: transparentize($--color-red-100, 0.1);
            transition: $--transition-main;
            text-decoration: underline;
        }
    }

    .svg__icon {
        width: 20px;
        height: 20px;
        margin-right: 10px;
    }

    &-map {
        height: 100%;
        max-height: 350px;
        width: 100%;
        object-fit: cover;
        margin: 40px 0;
    }
}

.branches {
    margin: 0;
    display: flex;
    flex-wrap: wrap;

    li {
        padding: 10px 20px;
        margin: 0;

        &:before {
            display: none;
        }
    }

    &__table {
        max-width: 850px;

        &-item {
            display: grid;
            align-items: center;
            padding: 15px 0;
            border-bottom: 1px solid transparentize($--color-black-100, 0.9);

            @include before('sm') {
                grid-row-gap: 10px;
                align-items: flex-start;
            }

            @include after('sm') {
                grid-template-columns: repeat(auto-fill, 33.33%);
            }
        }

        &-city {
            font-weight: $--font-weight-medium;
        }

        &-phone,
        &-email {
            color: $--color-black-100;
        }

        &-email {
            text-align: right;
        }
    }
}




/*accordion*/
details {
    position: relative;
    min-height: auto;
    max-height: 100%;
    transition: min-height .15s linear, max-height .5s linear;
    -webkit-transition: min-height .15s linear, max-height .5s linear;
    will-change: max-height;
    overflow: hidden;

    summary {
        display: flex;
        cursor: pointer;
        outline: 0;
        justify-content: flex-start;
        align-items: center;
        position: relative;

        @include before('sm') {
            flex-direction: column;
            align-items: flex-start;
        }

        &::-webkit-details-marker {
            display: none;
        }

        &::-moz-list-bullet {
            list-style-type: none;
        }

        &::before {
            content: "";
            transition: transform .1s linear;
            -webkit-transition: transform .1s linear;
            /*background-image: url(/img/arrowBottomIcon.svg);*/
            background-repeat: no-repeat;
            background-size: 15px 15px;
            margin-left: 10px;
            width: 15px;
            height: 15px;
            order: 3;
        }

        &::after {
            content: '(раскрыть список)';
            font-style: italic;
            color: $--color-red-100;
            font-weight: $--font-weight-medium;
        }

        h4 {
            margin-right: 10px;

            @include before('sm') {
                margin-bottom: 0;
            }
        }
    }

    &[open] {
        min-height: auto;
        max-height: 100%;

        summary {

            &~* {
                opacity: 1;
            }

            &:before {
                transform: rotate(180deg);
                -webkit-transform: rotate(180deg);
                -moz-transform: rotate(180deg);
            }

            &::after {
                content: '(скрыть список)';
            }
        }
    }
}




.scrollup {
    position: fixed;
    background-color: $--color-red-100;
    right: 20px;
    bottom: 20px;
    width: 40px;
    height: 40px;
    cursor: pointer;
    display: none;

    .svg__icon {
        width: 30px;
        height: 30px;
    }
}



#preloader {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background: #fff;
    z-index: 100;
}

#preloader .icon_animate {
    position: absolute;
    width: 250px;
    height: 114px;
    background: url(../img/logo_loader.svg) center center no-repeat;
    background-size: 250px 114px;
    /* centering begin */
    margin-left: auto;
    margin-right: auto;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* centering end */
}